body{
  margin:0;
  background-color:#f2f2f2;
}

.container{
  padding: 0 0 0 15px;
}

#spacex .data-container {
  width: 100%;
  margin: auto;
}

#spacex .data-container .col-filter{
  width: 100%;
}

#spacex .data-container .col-data{
  width: 100%;
}

#spacex .data-container .col-filter .filter {
  width: 100%;
  margin: auto;
  text-align: center;
  /* border: 1px solid black; */
  background-color: #fff;
  padding: 10px;
}

#spacex .data-container .col-data .data{
  width: 100%;
  margin: auto;
}

#spacex .data-container .col-data .data .card {
  width: 100%;
  height: 514px !important;
}

#spacex .data-container .col-data .data .card .card-details {
  width: 95%;
  /* border: 1px solid #000; */
  background-color: #fff;
  margin: 5px auto;
  text-align: center;

}

#spacex .float-left {
  float: left;
}

.header {
  width: 100% !important;
  display: inline-block;
}

#spacex .col-filter .filter .header .col {
  width: 50% !important;
  float: left;
  text-align: left;
}

#spacex .col-filter .filter .header .col.name {
  font-size: 20px;
  font-weight: bold;
}

#spacex .col-filter .filter .header .col.name:after {
  content: "";
  border-bottom: 3px solid #000000;
  width: 30px;
  display: block;
}

#spacex .col-filter .filter .header .col .reset-btn {
  height: 30px;
  background-color: white !important;
  display: none;
  cursor: pointer;
  border:none;
  outline:none;
  border-radius:5px;
  margin:5px 0px;
  font-size:17px;
  text-align: right;
  color: #66a6ff;
}
/* 
#spacex .col-filter .filter .header .col .reset-btn:hover {
  background-color: #7dba02;
  transform: translateY(-3px);
} */

input[type="radio"] {
  display: none;
}

input[type="radio"] + span {
  display: inline-block;
  position: relative;
  top: -1px;
  width: 100px;
  height: 30px;
  margin: 5px;
  vertical-align: middle;
  background-color: #c6e194;
  border: 1px solid #ccc;
  cursor: pointer;
}

input[type="radio"]:checked + span {
  background: #7dba02 -19px top no-repeat;
}

input[type="radio"] + span {
  margin-right: 4px;
}
input[type="radio"] + span:hover {
 transform: translateY(-3px);
}

/* 
* Screen widths below 700px (Mobile) 
*/
@media (max-width:700px) {
  #spacex .data-container .col-filter{
    width: 100% !important;
  }
  
  #spacex .data-container .col-data{
    width: 100% !important;
  }
  
  #spacex .data-container .col-filter .filter,
  #spacex .data-container .col-data .data {
    width: 60% !important;
  }
  
  #spacex .data-container .col-data .data .card {
    width: 100% !important;
  }
}
 
/* 
* Screen widths between 701px and 1024px (Tablets...) 
*/
@media (min-width: 701px) and (max-width: 1024px) {
  #spacex .data-container .col-filter{
    width: 35% !important;
  }
  
  #spacex .data-container .col-data{
    width: 65% !important;
  }
  
  #spacex .data-container .col-filter .filter,
  #spacex .data-container .col-data .data {
    width: 96% !important;
  }
  
  #spacex .data-container .col-data .data .card {
    width: 50% !important;
  }
}

/* 
* Screen widths larger than 1024px (Desktops...)
*/
@media (min-width: 1025px) {
  #spacex .data-container .col-filter{
    width: 20% !important;
  }
  
  #spacex .data-container .col-data{
    width: 80% !important;
  }
  
  #spacex .data-container .col-filter .filter,
  #spacex .data-container .col-data .data {
    width: 96% !important;
  }
  
  #spacex .data-container .col-data .data .card {
    width: 25% !important;
  }
}

.container .dev-details {
  font-size: 15px;
  text-align: center;
  bottom:0;
}
.reset-filter-btn{
  display: none;
}